@OptIn(ExperimentalResourceApi::class, ExperimentalComposeUiApi::class)
@Composable
fun EbookDetails(
    component: EbookComponent
) {
    val screenWidth = with(LocalDensity.current) {
        LocalWindowInfo.current.containerSize.width.toDp()
    }
    Row(
        modifier = Modifier.fillMaxWidth()
            .background(MaterialTheme.colorScheme.surface)
            .padding(
                start = screenWidth * 0.079f,
                bottom = MaterialTheme.dimens.spacing.extraLarge * 2
            )
    ) {
        Column(
            modifier = Modifier.fillMaxWidth(0.68f)
                .padding(end = MaterialTheme.dimens.spacing.xxxLarge)
        ) {
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.extraLarge * 2))
            Text(
                text = stringResource(Res.string.label_ebook_details_1),
                style = MaterialTheme.typography.headlineMedium
            )
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.extraLarge))
            Text(
                text = stringResource(Res.string.label_ebook_details_2),
                style = MaterialTheme.typography.bodyMedium
            )
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.xxxLarge))
            Row(
                modifier = Modifier.fillMaxWidth()
            ) {
                EbookModuleDescription(
                    modifier = Modifier.weight(1f,false),
                    moduleSubtitle = stringResource(Res.string.subtitle_ebook_module_1),
                    moduleTitle = stringResource(Res.string.title_ebook_module_1),
                    moduleItems = stringArrayResource(Res.string.ebook_module_1_items)
                )
                Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.xxxLarge))
                EbookModuleDescription(
                    modifier = Modifier.weight(1f,false),
                    moduleSubtitle = stringResource(Res.string.subtitle_ebook_module_2),
                    moduleTitle = stringResource(Res.string.title_ebook_module_2),
                    moduleItems = stringArrayResource(Res.string.ebook_module_2_items)
                )
                Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.xxxLarge))
                EbookModuleDescription(
                    modifier = Modifier.weight(1f,false),
                    moduleSubtitle = stringResource(Res.string.subtitle_ebook_module_3),
                    moduleTitle = stringResource(Res.string.title_ebook_module_3),
                    moduleItems = stringArrayResource(Res.string.ebook_module_3_items)
                )
            }
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.xxxLarge))
            PurchaseButton(
                text = buildAnnotatedString {
                    withStyle(style = SpanStyle(fontWeight = FontWeight.Normal)) {
                        append(stringResource(Res.string.action_buy_ebook))
                    }
                    withStyle(SpanStyle(fontWeight = FontWeight.Bold)) {
                        append(stringResource(Res.string.action_buy_ebook_price))
                    }
                },
                onPurchaseClick = {  }
            )
        }
        Box(
            contentAlignment = Alignment.TopEnd
        ) {
            Image(
                modifier = Modifier
                    .width(screenWidth * 0.221f)
                    .offset(y = MaterialTheme.dimens.spacing.extraLarge + screenWidth * 0.053f),
                painter = painterResource(Res.drawable.decorative_dashed_circle_3),
                contentDescription = null
            )
            Image(
                modifier = Modifier
                    .width(screenWidth * 0.284f)
                    .offset(y = MaterialTheme.dimens.spacing.extraLarge + screenWidth * 0.093f),
                painter = painterResource(Res.drawable.product_image_ebook),
                contentDescription = null
            )
        }
    }
}

@Composable
fun EbookModuleDescription(
    modifier: Modifier,
    moduleSubtitle: String,
    moduleTitle: String,
    moduleItems: List<String>,
) {
    Column(
        modifier = modifier
    ) {
        Text(
            text = moduleSubtitle,
            style = MaterialTheme.typography.bodyMedium,
            color = MaterialTheme.colorScheme.onSurfaceVariant
        )
        Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.extraSmall))
        Text(
            text = moduleTitle,
            style = MaterialTheme.typography.headlineSmall,
        )
        Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.small))

        Text(
            text = moduleItems.toBulletPoints(),
            style = MaterialTheme.typography.bodyMedium,
        )
    }
}