@OptIn(ExperimentalResourceApi::class)
@Composable
fun Toolbar(
    onAboutClick: () -> Unit,
    onProductsClick: () -> Unit,
    onFeedbacksClick: () -> Unit,
    onFaqClick: () -> Unit,
    onInstagramClick: () -> Unit,
    onYoutubeClick: () -> Unit,
) {
    Row(
        modifier = Modifier.fillMaxWidth()
            .background(MaterialTheme.colorScheme.background)
            .padding(
                horizontal = 90.dp,
                vertical = 32.dp
            ),
    ) {
        Image(
            modifier = Modifier.fillMaxWidth(0.13f),
            painter = painterResource(Res.drawable.logo),
            contentDescription = stringResource(Res.string.cd_logo)
        )
        Spacer(Modifier.weight(1.0f, true))
        Row(
            verticalAlignment = Alignment.CenterVertically
        ) {
            toolbarActions(
                onAboutClick = onAboutClick,
                onProductsClick = onProductsClick,
                onFeedbacksClick = onFeedbacksClick,
                onFaqClick = onFaqClick,
            )
            Spacer(modifier = Modifier.width(40.dp))
            socioalMediaActions(
                onInstagramClick = onInstagramClick,
                onYoutubeClick = onYoutubeClick
            )
        }
    }
}

@OptIn(ExperimentalResourceApi::class)
@Composable
fun RowScope.toolbarActions(
    onAboutClick: () -> Unit,
    onProductsClick: () -> Unit,
    onFeedbacksClick: () -> Unit,
    onFaqClick: () -> Unit,
) {
    TextButton(onClick = onAboutClick) {
        Text(
            text = stringResource(Res.string.toolbar_label_about),
            style = MaterialTheme.typography.bodyMedium
        )
    }
    Spacer(modifier = Modifier.width(16.dp))
    TextButton(onClick = onProductsClick) {
        Text(
            text = stringResource(Res.string.toolbar_label_products),
            style = MaterialTheme.typography.bodyMedium
        )
    }
    Spacer(modifier = Modifier.width(16.dp))
    TextButton(onClick = onFeedbacksClick) {
        Text(
            text = stringResource(Res.string.toolbar_label_feedback),
            style = MaterialTheme.typography.bodyMedium
        )
    }
    Spacer(modifier = Modifier.width(16.dp))
    TextButton(onClick = onFaqClick) {
        Text(
            text = stringResource(Res.string.toolbar_label_faq),
            style = MaterialTheme.typography.bodyMedium
        )
    }
}

@OptIn(ExperimentalResourceApi::class)
@Composable
fun RowScope.socioalMediaActions(
    onInstagramClick: () -> Unit,
    onYoutubeClick: () -> Unit,
) {
    Image(
        modifier = Modifier
            .size(MaterialTheme.dimens.iconSize.medium)
            .padding(MaterialTheme.dimens.spacing.small)
            .clickable {
                onInstagramClick()
            },
        colorFilter = ColorFilter.tint(MaterialTheme.colorScheme.primary),
        painter = painterResource(Res.drawable.instagram),
        contentDescription = stringResource(Res.string.cd_instagram)
    )
    Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.medium))
    Image(
        modifier = Modifier
            .size(MaterialTheme.dimens.iconSize.medium)
            .padding(MaterialTheme.dimens.spacing.small)
            .clickable {
                onYoutubeClick()
            },
        colorFilter = ColorFilter.tint(MaterialTheme.colorScheme.primary),
        painter = painterResource(Res.drawable.youtube),
        contentDescription = stringResource(Res.string.cd_youtube)
    )
}