private const val YOUTUBE_LINK = "https://www.youtube.com/@AndrezaNareal"
private const val MENTORY_LINK = "https://www.youtube.com/@AndrezaNareal"
private const val EBOOK_LINK = "https://www.youtube.com/@AndrezaNareal"
private const val POSITION_ABOUT_THE_PROGRAM = 1
private const val POSITION_PRODUCTS = 4
private const val POSITION_FEEDBACKS = 7
private const val POSITION_FAQ = 8

@OptIn(ExperimentalComposeUiApi::class)
@Composable
fun App(component: RootComponent) {
    val screenWidth = with(LocalDensity.current) {
        LocalWindowInfo.current.containerSize.width.toDp()
    }
    val dimens = when {
//        screenWidth < 600.dp -> AdaptiveCompactDimens
//        screenWidth < 900.dp -> AdaptiveMediumDimens
        screenWidth < 1100.dp -> CompactDimens
        screenWidth < 1300.dp -> LowerMediumDimens
        screenWidth < 1600.dp -> MediumDimens
        else -> ExpandedDimens
    }
    CompositionLocalProvider(LocalDimens provides dimens) {
        MaterialTheme(
            colorScheme = LightColors,
            typography = Typography(LightColors),
        ) {
            RootContent(component, Modifier.fillMaxSize())
        }
    }
}
