@OptIn(ExperimentalResourceApi::class, ExperimentalComposeUiApi::class)
@Composable
fun HomeSection(
    onAboutClick: () -> Unit,
    onProductsClick: () -> Unit,
    onFeedbacksClick: () -> Unit,
    onFaqClick: () -> Unit,
    onInstagramClick: () -> Unit,
    onYoutubeClick: () -> Unit,
    onStartJourneyClick: () -> Unit,
    onPlayClick: () -> Unit,
) {

    Column {
        val screenWidth = with(LocalDensity.current) {
            LocalWindowInfo.current.containerSize.width.toDp()
        }

        Box(
            modifier = Modifier.fillMaxWidth()
        ) {
            Toolbar(
                onAboutClick = onAboutClick,
                onProductsClick = onProductsClick,
                onFeedbacksClick = onFeedbacksClick,
                onFaqClick = onFaqClick,
                onInstagramClick = onInstagramClick,
                onYoutubeClick = onYoutubeClick,
            )
            Image(
                modifier = Modifier.fillMaxWidth(0.35f),
                painter = painterResource(Res.drawable.decorative_line_1),
                contentDescription = null
            )
            val imageOffset = screenWidth * 0.41f * 0.047f
            Row(
                modifier = Modifier.fillMaxWidth()
            ) {
                Column(
                    modifier = Modifier.weight(1f, fill = true)
                        .padding(
                            start = screenWidth * 0.079f,
                            top = screenWidth * 0.106f + 70.dp,
                            end = screenWidth * 0.087f
                        )
                ) {
                    Text(
                        text = stringResource(Res.string.home_section_title),
                        style = MaterialTheme.typography.headlineLarge
                    )
                    var text by remember { mutableStateOf("") }
                    var isVisible by remember { mutableStateOf(false) }
                    val scope = rememberCoroutineScope()

                    LaunchedEffect(true) {
                        scope.launch {
                            while (isActive) {
                                getStringArray(Res.string.home_title_variations).forEach {
                                    isVisible = true
                                    it.forEach {
                                        text = buildString {
                                            append(text)
                                            append(it)
                                        }
                                        delay(100L)
                                    }
                                    var count = 0
                                    while (isActive && count < 7) {
                                        delay(700L)
                                        isVisible = !isVisible
                                        count++
                                    }
                                    isVisible = true
                                    for (i in 1..it.length) {
                                        text = it.substring(0, it.length - i)
                                        delay(70L)
                                    }
                                }
                            }
                        }
                    }
                    Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.large))

                    Row(
                        modifier = Modifier.height(IntrinsicSize.Min),
                        verticalAlignment = Alignment.Bottom
                    ) {
                        Text(
                            modifier = Modifier.weight(1f, true),
                            textAlign = TextAlign.End,
                            style = MaterialTheme.typography.displayLarge,
                            text = text
                        )
                        if (isVisible) {
                            Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.medium))
                            VerticalDivider(
                                modifier = Modifier.fillMaxHeight().scale(scaleX = 1f, scaleY = 1.25f),
                                color = MaterialTheme.colorScheme.primary,
                                thickness = 2.dp
                            )
                        } else {
                            Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.medium+ 2.dp))
                        }
                    }
                    Spacer(modifier = Modifier.height(24.dp))
                    Text(
                        style = MaterialTheme.typography.bodyLarge,
                        text = stringResource(Res.string.home_section_description)
                    )
                    Spacer(modifier = Modifier.height(32.dp))
                    Row(
                        verticalAlignment = Alignment.CenterVertically
                    ) {

                        PurchaseButton(
                            onPurchaseClick = onStartJourneyClick
                        )
                        Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.large))
                        Image(
                            modifier = Modifier
                                .size(MaterialTheme.dimens.iconSize.large)
                                .clip(CircleShape)
                                .clickable {
                                    onPlayClick()
                                },
                            painter = painterResource(Res.drawable.play),
                            contentDescription = stringResource(Res.string.cd_play_intro)
                        )
                        Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.medium))
                        Text(
                            text = stringResource(Res.string.label_play_intro),
                            style = MaterialTheme.typography.labelMedium,
                            color = MaterialTheme.colorScheme.onSurfaceVariant
                        )

                    }

                }
                Box(
                    modifier = Modifier.padding(top = screenWidth * 0.053f + 70.dp),
                    contentAlignment = Alignment.BottomEnd,
                ) {

                    Image(
                        modifier = Modifier.width(screenWidth * 0.12f)
                            .offset(y = screenWidth * 0.0326f),
                        painter = painterResource(Res.drawable.decorative_circle_1),
                        contentDescription = null
                    )

                    Image(
                        modifier = Modifier.width(screenWidth * 0.12f)
                            .offset(y = screenWidth * 0.0312f),
                        painter = painterResource(Res.drawable.decorative_dashed_circle_1),
                        contentDescription = null
                    )
                    Box {
                        Image(
                            modifier = Modifier.size(screenWidth * 0.084f)
                                .offset(x = screenWidth * 0.043f + 50.dp),
                            painter = painterResource(Res.drawable.decorative_circle_2),
                            contentDescription = null
                        )
                        Image(
                            modifier = Modifier.size(screenWidth * 0.084f)
                                .offset(x = screenWidth * 0.032f + 50.dp),
                            painter = painterResource(Res.drawable.decorative_dashed_circle_2),
                            contentDescription = null
                        )
                        Image(
                            modifier = Modifier
                                .size(screenWidth * 0.41f)
                                .offset(imageOffset)
                                .clip(CircleShape),
                            painter = painterResource(Res.drawable.mentor_image),
                            contentDescription = null
                        )
                    }

                }
            }
        }
        Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.extraLarge + (screenWidth * 0.069f)))

        Row(
            modifier = Modifier.fillMaxWidth().padding(horizontal = 115.dp),
        ) {
            BenefitItem(
                modifier = Modifier.weight(1f, true),
                icon = Res.drawable.achievement,
                text = Res.string.label_guarantee
            )

            Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.large))

            BenefitItem(
                modifier = Modifier.weight(1f, true),
                icon = Res.drawable.shield,
                text = Res.string.label_secure_purchase
            )

            Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.large))

            BenefitItem(
                modifier = Modifier.weight(1f, true),
                icon = Res.drawable.academic_hat,
                text = Res.string.label_students_count
            )
        }
    }
}

@OptIn(ExperimentalResourceApi::class)
@Composable
fun BenefitItem(
    modifier: Modifier,
    icon: DrawableResource,
    text: StringResource
) {
    Surface(
        modifier = modifier,
        shape = RoundedCornerShape(MaterialTheme.dimens.cornerRadius.medium)
    ) {
        Row(
            modifier = Modifier.fillMaxSize()
                .padding(vertical = MaterialTheme.dimens.spacing.medium),
            horizontalArrangement = Arrangement.Center,
            verticalAlignment = Alignment.CenterVertically
        ) {
            Image(
                modifier = Modifier.size(MaterialTheme.dimens.iconSize.medium)
                    .padding(MaterialTheme.dimens.spacing.extraSmall),
                painter = painterResource(icon),
                contentDescription = null,
                colorFilter = ColorFilter.tint(MaterialTheme.colorScheme.primary),
            )
            Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.large))
            Text(
                text = stringResource(text),
                color = MaterialTheme.colorScheme.primary,
                style = MaterialTheme.typography.titleLarge,
                fontWeight = FontWeight.ExtraBold
            )
        }
    }
}