package ui

import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.lazy.LazyColumn
import androidx.compose.foundation.lazy.LazyListState
import androidx.compose.foundation.lazy.rememberLazyListState
import androidx.compose.material3.Scaffold
import androidx.compose.runtime.Composable
import androidx.compose.runtime.rememberCoroutineScope
import androidx.compose.ui.Modifier
import androidx.compose.ui.platform.LocalUriHandler
import kotlinx.coroutines.CoroutineScope
import kotlinx.coroutines.launch
import navigation.LandingComponent


private const val INSTAGRAM_LINK = "https://www.instagram.com/andrezanareal/"
private const val YOUTUBE_LINK = "https://www.youtube.com/@AndrezaNareal"
private const val MENTORY_LINK = "https://www.youtube.com/@AndrezaNareal"
private const val EBOOK_LINK = "https://www.youtube.com/@AndrezaNareal"
private const val POSITION_ABOUT_THE_PROGRAM = 1
private const val POSITION_PRODUCTS = 3
private const val POSITION_FEEDBACKS = 7
private const val POSITION_FAQ = 8

@Composable
fun Landing(component: LandingComponent) {
    Scaffold {
        val listState = rememberLazyListState()
        val scope = rememberCoroutineScope()
        val uriHandler = LocalUriHandler.current

        LazyColumn(
            modifier = Modifier.fillMaxSize(),
            state = listState
        ) {
            item(key = 1) {
                HomeSection(
                    onAboutClick = {
                        component.onMentoryClick()
                    },
                    onProductsClick = { scrollTo(scope, listState, POSITION_PRODUCTS) },
                    onFeedbacksClick = { scrollTo(scope, listState, POSITION_FEEDBACKS) },
                    onFaqClick = { scrollTo(scope, listState, POSITION_FAQ) },
                    onInstagramClick = { uriHandler.openUri(INSTAGRAM_LINK) },
                    onYoutubeClick = { uriHandler.openUri(YOUTUBE_LINK) },
                    onStartJourneyClick = { uriHandler.openUri(MENTORY_LINK) },
                    onPlayClick = { uriHandler.openUri(YOUTUBE_LINK) },
                )
            }
            item(key = 2) {
                ExchangeDetails()
            }
            item(key = 3) {
                ExchangeBenefits(
                    onStartJourneyClick = { uriHandler.openUri(MENTORY_LINK) }
                )
            }
            item(key = 4) {
                Products()
            }
            item(key = 5) {
                AboutMentor()
            }
            item(key = 6) {
                Feedback()
            }
            item(key = 7) {
                Footer(
                    onAboutClick = {
                        scrollTo(
                            scope,
                            listState,
                            POSITION_ABOUT_THE_PROGRAM
                        )
                    },
                    onProductsClick = { scrollTo(scope, listState, POSITION_PRODUCTS) },
                    onFeedbacksClick = { scrollTo(scope, listState, POSITION_FEEDBACKS) },
                    onFaqClick = { scrollTo(scope, listState, POSITION_FAQ) },
                    onInstagramClick = { uriHandler.openUri(INSTAGRAM_LINK) },
                    onYoutubeClick = { uriHandler.openUri(YOUTUBE_LINK) }
                )
            }
        }
    }
}


private fun scrollTo(scope: CoroutineScope, state: LazyListState, pos: Int) {
    scope.launch {
        state.animateScrollToItem(pos)
    }
}
